@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin horizontal-list {
  @include reset-list;
  li {
    display: inline-block;
  }
}

@mixin no-text-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; 
  }
  @-moz-keyframes #{$name} {
    @content;
  }
  @-ms-keyframes #{$name} {
    @content;
  }
  @keyframes #{$name} {
    @content;
  } 
}

@mixin filter-additional-border {
  border-right: 1px solid rgba(204, 204, 204, 0.5);
  padding-right: $px-16;
}

// Scroll bar ==========
@mixin scrollbar {
  width: 6px;
  height: 6px;
  background: #fcfcfc;
  border-radius: 9px;
}

@mixin scrollbar-track {
  border-radius: 9px;
  background: #fcfcfc;
}

@mixin scrollbar-thumb {
  border-radius: 9px;
  background: #ebebeb;
}