@import "../../layout/variables";
@import "../../layout/mixins";
.page-header {
  position: relative;
  z-index: 4;
  padding: 30px 0 1px 30px;
  background-image: url(../../assets/images/background-pattern.svg);
  .breadcrumbs {
    color: $white;
    margin-bottom: $px-16;
    svg {
      width: 22px;
      height: auto;
      margin-right: $px-8;
    }
    a {
      color: $white;
    }
    ul {
      @include reset-list;
      display: inline-block;
      vertical-align: middle;
      li {
        display: inline-block;
        font-size: $px-12;
        text-transform: uppercase;
        letter-spacing: 2px;
        &:after {
          position: relative;
          display: inline-block;
          content: ">";
          margin: 0 $px-8;
        }
        &:last-child {
          &:after {
            content: none;
          }
        }
      }
      span {
        display: inline-block;
        font-size: $px-12;
        text-transform: uppercase;
        letter-spacing: 2px;
      }
    }
  }
  .title {
    color: $white;
    margin-bottom: $px-16;
    text-transform: uppercase;
    h2 {
      margin: 0;
      font-weight: $font-weight-s;
      font-size: $px-30;
      letter-spacing: 2px;
    }
  }
  .inner-page {
    a {
      color: $white;
      min-width: 148px;
      text-align: center;
      padding: 0.4rem 1.4rem;
      text-transform: uppercase;
      display: block;
      &:hover {
        text-decoration: none;
      }
    }
    ul {
      @include reset-list;
      li {
        display: inline-block;
        margin-right: $px-16;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}

.dark-orange {
  background-color: $dark-orange;
  .inner-page {
    a {
      background: $tab-orange;
      &.active {
        background: $tab-orange-active;
      }
    }
  }
}

.turquoise {
  background-color: $turquoise;
  .inner-page {
    a {
      background: $tab-turquoise;
      &.active {
        background: $tab-turquoise-active;
      }
    }
  }
}

.green {
  background-color: #619061;
}
