        @import '../../layout/variables';
        .home-page {
            div.table {
                display: table;
            }
            
            .tr {
                display: table-row;
            }
            
            .td {
                display: table-cell;
                width: 40px;
            }
            
            .td .indicator,
            .period {
                position: absolute;
            }
            .td.val{
                width: 65px;
            }
            
            .td:nth-child(2) {
                width: 70px;
                font-size: $px-26;
            }
            
            .td:nth-child(3) {
                font-size: $px-12;
            }
            .home-img {
                margin-top: 2rem;
                padding-left: 3rem;
            }
            .title {
                padding: 2rem 3rem 3rem 0;
            }
            .gray {
                background-color: #818E94;
            }
            .page-content {
                padding-left: 30px!important;
            }
            .welcome-container {
                color: #1E1C24;
                display: table;
                height: 100%;
                overflow: hidden;
            }
            .welcome-content {
                display: table-cell;
                vertical-align: middle;
            }
            .header,
            .sub-header {
                padding-left: 2rem;
            }
            .header {
                font-weight: $font-weight-b;
                font-size: $px-30;
            }
            .sub-header {
                padding-top: $px-16;
                font-weight: $font-weight-m;
                font-size: $px-18;
            }
            .card-title {
                margin: 0;
                margin-bottom: $px-16;
                font-size: $px-12;
                letter-spacing: 2px;
                text-transform: uppercase;
                font-weight: $font-weight-s;
                color: $secondary-color;
            }
            .btn-gray {
                border: #DBDBDB;
                background-color: #DBDBDB;
                color: #8A8B8D;
                line-height: $px-8;
                border-radius: $px-16;
            }
            .table-responsive {
                overflow: hidden;
                .table thead th {
                    border: none;
                    font-weight: $font-weight-m;
                    font-size: 0.625rem;
                    padding: $px-8;
                    outline: none;
                    text-transform: uppercase;
                    color: $secondary-color;
                }
            }
            .icon {
                color: #818E94;
                font-size: 1.7rem;
            }
            .height1 td {
                padding: $px-16 $px-8;
                border-bottom: 1px solid rgba(116, 138, 157, 0.2);
            }
            .div-table {
                .val {
                    width: 70px;
                }
                .custom-width {
                    width: 40px;
                }
                .td:nth-child(1) {
                    font-size: $px-26;
                }
                .td:nth-child(3) {
                    font-size: $px-12;
                }
            }
            .td-content {
                padding: $px-8;
                .td-title {
                    font-size: 0.7rem;
                    text-transform: uppercase;
                    font-weight: $font-weight-s;
                    color: $secondary-color;
                }
                .td-text {
                    font-weight: $font-weight-m;
                    color: #737373;
                    font-size: $px-16;
                }
                .td-date {
                    font-size: 0.6rem;
                    text-transform: uppercase;
                    color: #737373;
                }
            }
        }