@import "../../layout/variables";
.legend-chart {
    position: absolute;
    margin-top: $px-8;
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        li {
            float: left;
            margin-right: 8px;
        }
        .legend-line {
            display: block;
            float: left;
            border: none;
            width: $px-10;
            height: 3px;
            margin: 8px 3px 0 3px;
        }
        .legend-color {
            display: block;
            float: left;
            border: 1px solid none;
            width: $px-10;
            height: $px-10;
            margin: 3px;
            border-radius: 50%;
        }
        .legend-name {
            float: right;
            margin: 0;
            font-size: $px-12;
            font-weight: $font-weight-m;
            text-transform: capitalize;
            color: $primary-color;
        }
        .smaller-font {
            font-size: 0.6rem;
            line-height: 1.1rem;
        }
    }
}