@import '../../../layout/variables';
.dashboard-container {
    .button-container {
        position: absolute;
        top: 5%;
        right: 5%;
        .btn-gray {
            >.btn {
                background-color: transparent;
                border-color: transparent;
                color: $primary-color;
                border-radius: $px-12;
                margin-left: $px-8;
                padding: 0.1rem 0.45rem;
                font-size: 1.1rem;
            }
            >.btn-primary:hover,
            >.btn-primary:focus,
            >.btn-primary.focus {
                color: $primary-color;
                background-color: #dbdbdb;
                border-color: $white;
            }
            >.btn-primary:not(:disabled):not(.disabled):active,
            >.btn-primary:not(:disabled):not(.disabled).active,
            .show>.btn-primary.dropdown-toggle {
                color: $primary-color;
                background-color: #dbdbdb;
                border-color: $white;
            }
        }
    }
    .card-body {
        .dropdown-toggle::after {
            display: none!important;
        }
        .dropdown-toggle::before {
            display: none!important;
        }
    }
    .large-card {
        height: 100%;
        .d-flex.flex-column {
            width: 50%;
            height: 100%;
        }
        .flex-column:first-child {
            overflow: hidden;
        }
    }
    .kpi-container-sm {
        height: 25px;
    }
  
    .chart-container-sm {
        height: -moz-calc(100% - 20px);
        height: -webkit-calc(100% - 20px);
        height: calc(100% - 20px);
    }
    .chart-container-donut-sm {
        height: -moz-calc(100% - 2px);
        height: -webkit-calc(100% - 2px);
        height: calc(100% - 2px);
    }
    .two-kpi-container-md {
        height: 85px;
    }
    .two-kpi-chart-container-md {
        height: -moz-calc(100% - 90px);
        height: -webkit-calc(100% - 90px);
        height: calc(100% - 90px);
        padding-top: 1.5rem
    }
    .four-kpi-container-md {
        height: 130px;
    }
    .four-kpi-chart-container-md {
        height: -moz-calc(100% - 125px);
        height: -webkit-calc(100% - 125px);
        height: calc(100% - 125px);
        padding-top: 1.5rem
    }
    .e-panel {
        .card-title {
            margin-bottom: $px-16;
            font-size: $px-10;
        }
        .card-body {
            height: 93%;
        }
        .small-card.line-height.card-body {
            height: 85%;
        }
        .small-card.card-body {
            height: 90%;
        }
        .heightTwo {
            height: calc(100%)
        }
    }
    .e-dashboardlayout.e-control {
        display: block;
        position: relative;
        z-index: 1;
    }
    .total-revenue {
        font-size: $px-20;
        span {
            font-size: $px-40;
        }
    }
    .total-revenue-sm {
        font-size: $px-14;
        span {
            font-size: $px-24;
        }
        span.sub-label {
            display: block;
            font-size: $px-12;
            margin: 0 0 40px 8px;
        }
    }
    .total-revenue-md {
        font-size: $px-16;
        span {
            font-size: $px-24;
        }
        span.sub-label {
            display: block;
            font-size: $px-12;
            margin: 0 0 40px 8px;
        }
    }
    .filter-title {
        width: 125px;
        color: inherit;
        font-weight: $font-weight-s;
        font-size: 0.625rem;
        svg {
            float: right;
            margin-top: 0.2rem;
        }
    }
    .dropdown-submenu .btn {
        background-color: transparent;
        border-color: transparent;
        color: inherit;
        border-radius: 0;
        padding: 0.25rem 1.5rem;
        font-size: 0.9rem;
        text-align: left;
        border-radius: none;
        .dropdown-toggle {
            position: relative;
            display: block;
            margin-left: 0;
            width: 86%;
        }
    }
    .dropdown-submenu:hover {
        color: #16181b;
        text-decoration: none;
        background-color: #f8f9fa;
    }
    .dropdown-submenu {
        .btn:hover,
        .btn:focus {
            color: #16181b;
            text-decoration: none;
            background-color: #f8f9fa;
            box-shadow: none;
            svg {
                fill: #16181b;
            }
        }
        .btn:focus,
        .btn:active {
            background-color: $orange;
            color: $white;
            svg {
                fill: $white;
            }
        }
    }
    .kpi-item {
        .val span {
            font-size: 1.1rem;
        }
        .title,
        .center-val {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .overall-bps {
        font-size: 1rem;
        .overall-bps-positive {
            color: $green;
        }
        .overall-bps-negative {
            color: $red;
        }
        .overall-bps-middle {
            color: $yellow;
        }
    }
    .with-ellipsis {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .custom-scroll {
        &::-webkit-scrollbar {
            width: 2px;
            height: 2px;
            background: transparent;
            border-radius: 9px;
        }
        &::-webkit-scrollbar-track {
            border-radius: 2px;
            background: transparent;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 9px;
            background: transparent;
        }
    }
    .medium-card {
        .kpi-item {
            padding-left: 0;
        }
        .title {
            font-size: 0.65rem;
            padding-top: 8px;
        }
        .value {
            font-size: $px-16;
        }
    }
}