@import '../../layout/variables';
.kpi-item {
    padding-left: $px-10;
    .title {
        font-size: $px-12;
        padding-top: 8px;
    }
    .val {
        font-size: $px-12;
        width: 140px;
        vertical-align: middle;
        padding-bottom: 0.2rem;
    }
    .val-unit {
        font-size: $px-12;
    }
    .val span:nth-child(1) {
        font-size: $px-26;
    }
    .center-val {
        margin: 0 auto;
    }
}

.no-hover:hover {
    background-color: none;
}

.with-hover:hover {
    background-color: #f7f8fb;
}

.middle {
    vertical-align: middle;
    padding-bottom: 0.4rem;
}

.border-bottom {
    border-bottom: 1px solid gainsboro;
    padding-bottom: 0.25rem;
    margin-bottom: $px-8;
}

div.table {
    display: table;
}

.tr {
    display: table-row;
}

.td {
    display: table-cell;
    width: 78px;
}

.td .indicator,
.period {
    // position: absolute;
}

.period {
    font-size: $px-12;
}

.td:nth-child(2) {
    width: 100px;
    text-align: center;
    // font-size: $px-26;
}

// .td:nth-child(3) {
//     font-size: $px-12;
// }
.total-revenue {
    font-size: $px-20;
    span {
        font-size: $px-50;
    }
    span.sub-label {
        display: block;
        font-size: $px-12;
        margin: 0 0 40px 8px;
    }
}

.overall-bps-positive {
    color: $green;
}

.overall-bps-negative {
    color: $red;
}

.overall-bps-middle {
    color: $yellow;
}