// Colors
$black: #000;
$white: #fff;
$orange: #cd503c;
$light-orange: #dc7431;
$dark-blue-green: #39484c;
$green: #00b050;
$red: #ff3333;
$yellow: #f0cc71;
$dark-orange: #c54d39;
$turquoise: #307f8c;
$gray: #a1a1a4;
$purple: #7334a2;

// Background
$bg-green: rgba($green, 0.1);
$bg-red: rgba($red, 0.1);
$bg-yellow: rgba($yellow, 0.1);
$bg-light-orange: rgba($light-orange, 0.1);
$bg-dark-orange: rgba($dark-orange, 0.1);
$bg-gray: rgba($gray, 0.1);

// Button
$btn-border-color: #efefef;
$btn-background: #fafafa;
$btn-background-active: $orange;
$btn-border-radius: 4px;

// PX to Rem
$px-8: 0.5rem;
$px-10: 0.625rem;
$px-12: 0.75rem;
$px-14: 0.875rem;
$px-16: 1rem;
$px-18: 1.125rem;
$px-20: 1.25rem;
$px-22: 1.375rem;
$px-24: 1.5rem;
$px-26: 1.625rem;
$px-28: 1.75rem;
$px-30: 1.875rem;
$px-32: 2rem;
$px-34: 2.125rem;
$px-36: 2.25rem;
$px-38: 2.375rem;
$px-40: 2.5rem;
$px-42: 2.625rem;
$px-44: 2.75rem;
$px-46: 2.875rem;
$px-48: 3rem;
$px-50: 3.125rem;
$px-60: 3.75rem;
$px-70: 4.375rem;
$px-80: 5rem;

// Font Weight
$font-weight-l: 300;
$font-weight-r: 400;
$font-weight-m: 500;
$font-weight-s: 600;
$font-weight-b: 700;

// Default
$Montserrat: "Montserrat", sans-serif;
$primary-color: #757678;
$secondary-color: #748a9d;
$box-shadow-color: #ff9b76;
$light-text-color: #bfbfbf;
$dark-text-color: #4d4f5c;
$darker-text-color: #43425d;
$border-color: #d8d8d8;

// Table
$tr-hover: #f7f8fb;

// Tabs
$tab-orange: #c5503d;
$tab-orange-active: #df6955;
$tab-turquoise: #35828e;
$tab-turquoise-active: #4d9ba8;

// Filter Options
$separator-color: #ccc;

// Comment Area
$comment-background: #f0f4f8;
$comment-footer-border-color: #eaf0f4;
$mention-color: #307f8c;