@import "../../layout/variables";
@import "../../layout/mixins";

.product-modal {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  .modal-dialog {
    width: 1000px;
  }
  .modal-content {
    background: transparent;
    border-color: transparent;
  }
  .to-prevent-glitch {
    height: $px-16;
    width: $px-16;
    background: transparent;
    color: transparent;
  }
  .content-wrapper {
    display: flex;
    flex-direction: row;
  }
  .content-container {
    border-radius: 8px;
    border-color: $border-color;
    background: $white;
    box-shadow: 0 0 $px-10 rgba(0, 0, 0, 0.1);
    min-width: 1000px;
    &.step-1 .btn-container {
      .btn {
        min-height: 164px;
        .icon {
          margin-bottom: $px-16;
          height: 60px;
        }
      }
    }
    .responsive-basic-dd {
      position: absolute;
      top: 80px;
      text-align: center;
      transform: translateY(-50%);
      right: 1.8rem;
    }
  }
  .modal-header {
    padding: 2.5rem 2.5rem 1.5rem;
    align-items: center;
    border-bottom: none;
    position: relative;
    .modal-title {
      font-size: $px-16;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: $secondary-color;
      font-weight: $font-weight-s;
    }

    .form-control {
      font-size: $px-12;
      border-color: rgba(116, 138, 157, 0.2);
      padding: 0.4rem 0.8rem;

      &:focus {
        border-color: #e2998d;
      }
    }
  }
  .modal-body {
    padding: 0 1.8rem;
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    max-height: 550px;
    overflow-y: auto;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 6px;
      background: #fcfcfc;
      border-radius: 9px;
    }
    &::-webkit-scrollbar-track {
      border-radius: 9px;
      background: #fcfcfc;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 9px;
      background: #ebebeb;
    }
    .btn-container {
      width: 25%;
      padding: $px-10;
      display: inline-block;
      .btn {
        min-height: 164px;
        .icon {
          margin-bottom: $px-16;
          height: 60px;
        }
      }
    }
    .btn {
      font-weight: $font-weight-l;
      font-size: $px-12;
      border-color: $border-color;
      width: 100%;
      border-radius: 8px;
      &:hover,
      &.active {
        background-color: $dark-orange;
        color: $white;
        fill: $white;
        .icon {
          fill: $white !important;
        }
      }
      .icon {
        display: block;
        margin: 0 auto;
        fill: $gray;
      }
    }
    .checkbox-true {
      background-color: $dark-orange;
      color: $white;
      fill: $white;
      .icon {
        fill: $white !important;
      }
    }
    .search-checkbox {
      width: 100%;
      font-size: $px-14;
      font-weight: $font-weight-s;
      .form-control {
        font-size: $px-12;
        margin-bottom: 1.6rem;
        &:focus {
          border-color: lighten($dark-orange, 20%);
          box-shadow: 0 0 0 0.2rem rgba(58, 176, 195, 0.5);
        }
      }
      .list-of-checkbox {
        width: 98%;
        margin: 0 auto;
        max-height: 430px;
      }
      .custom-checkbox {
        margin-bottom: 0.6rem;
        &.custom-control {
          min-height: 16px;
          padding-left: 30px;
        }
        &.custom-control .custom-control-label::before {
          top: 2px;
          left: -30px;
          width: 16px;
          height: 16px;
        }
        &.custom-control .custom-control-label::after {
          top: -2px;
          left: -33px;
          width: 23px;
          height: 23px;
        }
      }
      .custom-control-input:checked ~ .custom-control-label::before {
        border-color: $dark-orange;
        background-color: $dark-orange;
      }
      .custom-control-input {
        display: inline-block;
        padding: 5em;
        background: #eee;
        opacity: 1;
      }
    }
    .product-form{
        width: 100%;
    }
  }
  .modal-footer {
    padding: 1.5rem 2.5rem 2rem;
    border-top: none;
    .btn {
      font-size: $px-10;
      min-width: 66px;
    }
  }
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: none !important;
  }
}
