@import '../../layout/variables';
.executive-cockpit-page {
    .container-metrics-page {
        &.container-fluid {
            padding: 0 50px
        }
        .inner-wrap {
            height: calc( 100vh - 200px);
            min-height: 500px;
            position: relative;
        }
        .block {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }
        .content-block {
            padding-right: 5rem;
            .title {
                color: #1E1C24;
                font-size: 3.3rem;
                font-weight: $font-weight-b;
            }
            .text {
                color: #B1AAAA;
                font-size: 1.5rem;
                font-weight: $font-weight-m;
                line-height: 1.8;
                margin-top: $px-16;
                margin-right: $px-16;
            }
            .btn {
                border-radius: 999px;
                margin-top: $px-16;
                font-size: $px-18;
                padding-left: 3rem;
                padding-right: 3rem;
                &.btn-metrics {
                    margin-right: 2rem;
                }
            }
        }
        .image-block {
            .person {
                position: absolute;
                &.person-1 {
                    bottom: -32px;
                    left: -32px;
                }
                &.person-2 {
                    bottom: -32px;
                    right: 23px;
                }
            }
        }
    }
    .filter-container {
        padding-left: 0;
        .btn-group.right-group {
            width: auto;
            opacity: 1;
            transition: width 0.4s ease-in-out, opacity 0.1s ease-in-out;
        }
    }
}

.metrics-page {
    display: flex;
    align-items: center;
    .content {
        padding-left: 5rem;
        padding-right: 10rem;
    }
    .title {
        font-size: $px-50;
        color: #1E1C24;
        font-weight: $font-weight-b;
        line-height: normal;
    }
    .text {
        font-size: $px-18;
        color: #B1AAAA;
        font-weight: $font-weight-m;
        padding-top: $px-16;
    }
    .btn-metrics {
        border-radius: 2rem;
        padding: $px-8 4rem;
        margin-top: 2rem;
    }
}

.grid-stack {
    background: white;
}

.grid-stack-item-content {
    color: #2C3E50;
    text-align: center;
    background-color: #18BC9C;
}

.metrics-modal {
    &.modal {
        backdrop-filter: blur(6px);
    }
    &.modal-backdrop.show {
        opacity: 0.1;
    }
    &.modal .modal-dialog {
        transform: none;
        width: 1000px;
    }
    .modal-header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 2rem 2rem 0;
        border-bottom: none;
        border-top-left-radius: calc(0.3rem - 1px);
        border-top-right-radius: calc(0.3rem - 1px);
    }
    .modal-title {
        font-size: $px-12;
        text-transform: uppercase;
        font-weight: $font-weight-s;
        line-height: 2.5;
        letter-spacing: 2px;
        color: $secondary-color;
    }
    .narrow-list.filter-modal {
        display: block;
        .title {
            display: none;
        }
    }
    .custom-modal {
        width: 100%;
        z-index: 9999;
        padding: $px-12 2rem 0;
        background: $white;
        display: flex;
        flex-direction: column;
        box-shadow: none;
        margin: 0;
    }
    .narrow-list .btn.dropdown-toggle {
        width: 100%;
    }
    .btn-container {
        margin-left: auto;
        margin-right: 0;
        padding: 0 2rem 2rem;
    }
    .btn {
        font-size: 0.625rem;
    }
    .list-of-checkbox {
        min-height: 550px;
        margin-top: $px-16;
    }
    .modal-body .search-checkbox .form-control {
        font-size: $px-12;
        margin-bottom: 1.6rem;
        margin-top: $px-16;
    }
    .form-group {
        // max-height: $px-12;
        margin: 14px;
        border-radius: 8px;
        border: 1px solid gainsboro;
        // min-width: 200px;
        padding: $px-10;
        display: inline-block;
        // min-height: 164px;
        width: 200px;
        height: 180px;
        &:hover,
        &.active {
            background-color: $dark-orange;
            color: $white;
            fill: $white;
            .icon {
                fill: $white !important;
            }
        }
        input:active {
            background: none;
        }
        .custom-control-input:checked~.custom-control-label::before {
            border-color: transparent;
            background-color: transparent;
        }
        .custom-control-input:not(:disabled):active~.custom-control-label::before {
            border-color: transparent;
            background-color: transparent;
        }
        .custom-control-input:focus~.custom-control-label::before {
            box-shadow: none;
        }
        .custom-checkbox .custom-control-input:checked~.custom-control-label::after {
            background-image: none;
            // background-image: url(data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26l2.974 2.99L8 2.193z'/%3e%3c/svg%3e);
        }
        label:before {
            position: absolute;
            content: '';
            // margin-left: 2.5em;
            // margin-top: 1em;
            width: 100%;
            height: 3em;
            border: none;
            background: pink;
        }
        .custom-control {
            margin: 0 auto;
            text-align: center;
            padding-left: 0
        }
        label {
            // display: block;
            margin: 0 auto;
            fill: $gray;
            width: 80%;
        }
        svg {
            display: block;
            margin: 20px auto;
            fill: $gray;
            height: 60px
        }
    }
    // .custom-checkbox.custom-control {
    //     max-height: $px-12;
    //     margin-left: 18px;
    //     border: 1px solid gainsboro;
    //     min-width: 200px;
    //     padding: $px-10;
    //     display: inline-block;
    //     min-height: 164px;
    // }
}

.images {
    position: absolute;
    top: -40%;
    .laptop {
        height: 600px;
    }
    .person-1 {
        position: absolute;
        left: 0;
        top: 50%;
    }
    .person-2 {
        position: absolute;
        top: 65%;
        right: 0.75%;
    }
}

.draggable-div {
    cursor: 'move';
    background: $white;
    box-shadow: $px-10 $px-10 $px-10 0px rgba(212, 212, 212, 0.1);
    border: 1px solid $border-color;
    border-radius: $px-10;
    height: 100%;
}